.home,.footer, .footerRaid{
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
}
.home{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url("https://images3.alphacoders.com/114/1148631.jpg");
}
.home .reset, .home p{
  display: flex;
  align-items: center;
  justify-content: center;
  background: #00000040;
  box-shadow: 0 8px 32px 0 #ffffff5e;
  backdrop-filter: blur( 4px );
  -webkit-backdrop-filter: blur( 4px );
  border-radius: 10px;
  border: 1px solid #ffffff2e;
}
.home .reset{
  height: 500px;
  width: 70%;
  position: absolute;
}
.home p{
  padding: 10px 35px;
  width: 90%;
}
.footer{
  background-image: url("https://images6.alphacoders.com/103/1037690.png");
}
.footerRaid{
  background-image: url("https://wallpaperaccess.com/full/3808550.jpg");
}